/*Dashboard Layout*/

.dashboard-container {
    width: 100vw;
    max-width: 75rem;
}

.dashboard-container .main-container {
    box-sizing: border-box;
    padding: 2rem 3rem;
    display: inline-block;
    width: 80%;
    max-width: 60rem;
}

.dashboard-container .spacer {
    display: inline-block;
    width: 20%;
    max-width: 15rem;
}

.dashboard-container .heading-container {
    display: flex;
    color: #002341;
    height: 10%;
    max-height: 4.5rem;
    margin-bottom: 3rem !important;
}

.dashboard-container .heading-container * {
    display: inline-block;
    vertical-align: middle;
}

.dashboard-container .heading-container .heading-icon {
    height: 2.8rem;
}

.dashboard-container .heading-container h1 {
    margin: 0.1rem 0 0 1rem;
}

.dashboard-container .heading-container {
    margin: 0.1rem 0 0 1rem;
}

.dashboard-container .heading-container .menu {
    display: none;
}

/*Sidebar*/

.sidebar-container {
    background-color: #ffffff;
    box-shadow: var(--shadow-green);
    position: fixed;
    height: 100vh;
    width: 20%;
    max-width: 15rem;
    min-width: 10rem;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    z-index: 23;
    /* display: none; */
}

.sidebar-container.active {
    transform: translateX(0%);
}

.sidebar-container .item {
    width: 100%;
    height: 10%;
    max-height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar-container .item.top .close {
    display: none;
}

.sidebar-container .logo {
    margin: 0;
    width: 80%;
    max-width: 12rem;
}

/*Sidebar Menu*/

.sidebar-container .menu-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    height: 50%;
    max-height: 23rem;
}

/*Dashboard Main*/

.dashboardmain-container {
    width: 100%;
}

.dashboardmain-container .row {
    display: block;
    width: 100%;
    margin-bottom: 3rem;
}

.dashboardmain-container .row * {
    margin-bottom: 0.8rem;
}

.dashboardmain-container .header {
    font-size: 2rem;
    font-weight: 700;
    color: #00a473;
    width: 100%;
    text-align: left;
    padding: 1rem 0;
    margin-bottom: 2rem;
}

.dashboardinput-container {
    /* display: flex !important; */
    flex-flow: row wrap;
    width: 100%;
    justify-content: flex-start;
}

.dashboardinput-container * {
    margin-left: 1rem;
}

.dashboardinput-container label {
    font-size: 0.8rem;
    color: #002341;
}

.dashboardinput-container input,
.dashboardinput-container select {
    color: #002341;
    font-family: Nunito, sans-serif;
    font-weight: 700;
    box-sizing: border-box;
    font-size: 1.2rem;
    padding: 0.2rem 0.7rem 0.7rem 0.7rem;
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 2px solid #afcbe4;
}

.dashboardinput-container select {
    padding: 0.5rem;
    border: 3px solid #afcbe4;
}

.dashboardinput-container select:focus,
select:hover {
    color: #00a473;
    border: 3px solid #00a473;
}

.dashboardinput-container .field {
    flex-basis: 45%;
}

.dashboardinput-container .fieldbig {
    flex-basis: 45%;
}

.dashboardinput-container .uneditable {
    color: #869eb4;
}

.dashboardinput-container .editable:focus,
.dashboardinput-container .editable:hover {
    color: #00a473;
    border-bottom: 3px solid #00a473;
}

.dashboardinput-container select {
    padding: 0.5rem;
    border: 3px solid #afcbe4;
}

.dashboardinput-container select:focus,
.dashboardinput-container select:hover {
    color: #00a473;
    border: 3px solid #00a473;
}

.checkbox-field {
    flex-basis: 33%;
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
}

.dashboardcheckbox-container input {
    border: 1.3px solid #afcbe4;
    background-color: #ffffff;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
    width: 0.3rem;
    margin-left: 1rem;
    transform: scale(2);
    -ms-transform: scale(2);
    -webkit-transform: scale(2);
    padding: 0.3rem;
    line-height: 0;
    -webkit-transition: background-color 0.2s ease-out;
    -moz-transition: background-color 0.2s ease-out;
    -o-transition: background-color 0.2s ease-out;
    transition: background-color 0.2s ease-out;
}

.dashboardcheckbox-container h2 {
    display: inline-block !important;
    font-size: 1.2rem;
    width: 80%;
}

.dashboardcheckbox-container input:hover,
.dashboardcheckbox-container input:checked {
    cursor: pointer;
    border: 2px solid #00a473;
}

.dashboardcheckbox-container input:checked {
    background-color: #00a473;
}

#photo-upload,
#certificate-upload {
    display: none;
}

.dashboardtextarea {
    flex-basis: 100%;
    box-sizing: border-box;
}

.dashboardtextarea textarea {
    width: 100%;
    color: #002341;
    font-size: 1.2rem;
    line-height: 1.3rem;
    box-sizing: border-box;
    padding: 0.7rem;
    border: 3px solid #afcbe4;
}

.dashboardtextarea textarea:focus,
.dashboardtextarea textarea:hover {
    border: 3px solid #00a473;
    outline: none;
}

.dashboardtextarea h2 {
    font-size: 1.2rem;
}

/*Profile Picture*/

.dashboardmain-container .profilepicture-container {
    position: relative;
    box-sizing: border-box;
    max-width: 15rem;
    max-height: 15rem;
    height: 40vw;
    width: 40vw;
    border-radius: 15%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 101% 101%;
}

.dashboardmain-container .certificate-container {
    position: relative;
    max-width: 35rem;
}

/*doctor/prescriptions*/

.medicine-container,
.medicine-controller {
    margin-bottom: 0 !important;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.medicine-controller {
    margin-bottom: 0 !important;
}

.medicine-controller > div {
    margin-left: 0;
    margin-bottom: 0;
    display: inline-block;
}

.medicine-controller select,
.medicine-controller .field,
.medicine-controller .field * {
    margin: 0;
}

.medicine-display > h2 {
    font-size: 1.2rem;
}

.medicine-display {
    width: 100%;
    margin-bottom: 0 !important;
}

.medicine-display > h2 {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.medicine-display > h2 > span {
    flex-basis: 30%;
}

/*Medicine Popup*/

/* The Modal (background) */

.modal {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.3);
    left: 0;
    top: 0;
    opacity: 0;
    transition: 0.25s;
}

.modal.active {
    z-index: 45;
    opacity: 1;
}

.modal-content.active {
    z-index: 1;
    transform: scale(1);
    opacity: 1;
}

/* Modal Content */

.modal-content {
    box-sizing: border-box;
    border-radius: 1rem;
    padding: 2rem;
    background-color: #fefefe;
    width: 60%;
    transform: scale(0);
    opacity: 0;
    transition: 0.25s;
}

.modal-content .header {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 2.2rem;
}

.modal-content .header > * {
    margin-bottom: 0.7rem;
}

.modal-content h1 {
    font-size: 2rem;
}

.modal-content .info {
    margin-bottom: 1.8rem;
}

.modal-content > .info > h1 {
    text-transform: uppercase;
    color: #00a473;
    font-size: 1.5rem;
    margin-bottom: 0.9rem;
}

.modal-content .symptoms {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
}

.modal-content .symptoms > p {
    margin-right: 2rem;
    line-height: 2rem;
    flex-basis: 25%;
}

.modal-content .symptoms i {
    color: #4397e0;
    font-size: 1.2rem;
    margin-right: 0.3rem;
}

/* The Close Button */

.close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

/*Prescriptioning*/

.prescriptioning-container {
    width: 100%;
    display: flex;
}

/*Chat*/

.home-container {
    width: 100%;
    background-color: #ffffff;
    border-radius: 1rem;
    display: grid;
    grid-template-columns: repeat(3, 32%);
    grid-gap: 1rem;
    overflow-y: scroll;
    padding: 2rem;
    box-sizing: border-box;
    height: 70vh;
}

.home-container::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 1px rgba(0, 65, 38, 0.3);
    background-color: #ffffff;
    border-radius: 10px;
}

.home-container::-webkit-scrollbar {
    width: 10px;
    background-color: #f5f5f5;
}

.home-container::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-image: -webkit-gradient(
        linear,
        left bottom,
        left top,
        color-stop(0.44, rgb(179, 233, 208)),
        color-stop(0.72, rgb(106, 216, 152)),
        color-stop(0.86, rgb(133, 226, 164))
    );
}

.doctor-container {
    background-size: 200% auto;
    background-image: linear-gradient(281.8deg, #54da85 0%, #85ffbc 100%);
    box-shadow: 3px 3px 6px rgba(120, 255, 187, 0.5);
    flex-basis: 30%;
    border-radius: 1.5rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    padding: 1rem;
    box-sizing: border-box;
    align-items: center;
    height: 8rem;
    margin-bottom: 2rem;
    transform: scale(1);
    transition: transform 0.5s;
    transition: 0.5s;
    cursor: pointer;
}

.doctor-container:hover {
    background-position: right center;
    transform: scale(1.1);
}

.doctor-container-gradient {
    background-image: linear-gradient(to right, #6beb9a 0%, #37c281 51%, #76f8a6 100%);
}

.doctor-container .picture-container {
    flex-basis: 30%;
}

.doctor-container > .picture-container > div {
    border: 3px solid #ffffff;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 110% 110%;
}

.doctor-container .info-container {
    flex-basis: 40%;
    width: 100%;
    font-size: 1.2rem;
}

.doctor-container .info-container > * {
    width: 100%;
}

.doctor-container .info-container h1 {
    color: #ffffff;
    font-size: 1.2rem;
}

.doctor-container .info-container h3 {
    color: #c2ffe0;
    font-size: 0.8rem;
    margin-bottom: 0.4rem;
}

.chat-intro {
    width: 100%;
    margin: 1rem 0;
}

.chat-intro * {
    text-align: center;
}

.chat-intro .picture-container {
    height: 5rem;
    width: 5rem;
    margin: auto;
    margin-bottom: 1rem;
}

.chat-intro > .picture-container > div {
    border: 5px solid #05437e;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 6rem auto;
}

/*Paginating*/

inputPage {
    -webkit-appearance: none;
    -moz-appearance: textfield;
}
