@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700;800&display=swap);
:root {
    --shadow-green: 0px 0px 1rem rgba(30, 222, 125, 0.3);
    --shadow-blue: 0px 0px 1rem rgba(23, 123, 208, 0.3);
    --gradient-green: linear-gradient(281.8deg, #00a473 0%, #24e97e 100%);
    --gradient-blue: linear-gradient(281.8deg, #0076da 0%, #3799ef 100%);
}

html {
    font-size: 16px;
}

@media screen and (min-width: 900px) {
    html {
        font-size: 16px;
    }
}

@media screen and (min-width: 1200px) {
    html {
        font-size: 20px;
    }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
v2.0 | 20110126
License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

button {
    padding: 0;
}

body {
    line-height: 1;
    background-color: #f4fcff;
    overflow: hidden;
    overflow-y: scroll;
}

ol,
ul {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

body * {
    font-family: Nunito;
}

h1,
h2,
h3 {
    font-weight: 700;
}

h1 {
    font-size: 2.7rem;
}

h2 {
    font-size: 1rem;
}

a {
    text-decoration: none;
}

.icon {
    cursor: pointer;
    width: 2rem;
}

.button {
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    display: inline-block;
    padding: 0.8rem 1.8rem;
    border-radius: 0.4rem;
    transition: 0.1s;
    text-align: center;
    cursor: pointer;
    -webkit-transform: scale(1);
            transform: scale(1);
    border: none;
}

.button:hover {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

.button.green {
    background: linear-gradient(281.8deg, #00a473 0%, #24e97e 100%);
    background: var(--gradient-green);
    box-shadow: 0px 0px 1rem rgba(30, 222, 125, 0.3);
    box-shadow: var(--shadow-green);
}
#savemedicineform {
    margin-left: 2rem;
}
.button.blue {
    background: linear-gradient(281.8deg, #0076da 0%, #3799ef 100%);
    background: var(--gradient-blue);
    box-shadow: 0px 0px 1rem rgba(23, 123, 208, 0.3);
    box-shadow: var(--shadow-blue);
}
#doctorlogin,
#patientlogin {
    width: 100%;
}

.button-none {
    -webkit-appearance: none;
    -o-appearance: none;
    appearance: none;
    border: none;
    background-color: transparent;
}

.hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: 0.2s;
}

.hover:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

/*Home page*/

/*Navbar*/

.nav-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 100%;
    max-width: 75rem;
    margin: 1rem auto;
    max-height: 9rem;
    flex: 0 1;
}

.nav-container > * {
    flex-basis: 30%;
}

.nav-container a {
    text-decoration: none;
}

.nav-container .logo-container {
    display: flex;
    justify-content: center;
    z-index: 25;
}

.nav-container .logo {
    width: 80%;
    max-height: 70%;
    max-width: 18rem;
    padding: 2rem;
    z-index: 1;
    margin-left: 0;
    cursor: pointer;
}

.nav-container .menu {
    display: flex;
    flex: row 1 nowrap;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.nav-container .menu * {
    font-weight: 700;
    color: #002341;
    display: inline-block;
    padding: 1rem;
}

.nav-container .menu.mobile {
    z-index: 25;
    transition: 0.5s ease;
}

.nav-container .menu.mobile.active {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    display: none;
}

.nav-container .menu.desktop {
    display: flex;
}

.nav-container .button-container {
    display: flex;
    flex: row 1 nowrap;
    justify-content: center;
    align-items: center;
}

.nav-container .button-container.mobile {
    z-index: 25;
    transition: 0.5s ease;
}

.nav-container .button-container.mobile.active {
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
    display: none;
}

.nav-container .button-container.desktop {
    display: flex;
}

.nav-container .button-container #register {
    margin-left: 1rem;
}

.nav-container .hamburger {
    padding: 1rem;
    display: none;
    z-index: 2;
    cursor: pointer;
}

.nav-container .hamburger-bg {
    background: #ffffff;
    height: 100vh;
    width: 100vw;
    position: fixed;
    display: block;
    z-index: 15;
    transition: 0.5s ease;
}

.hamburger-bg.active {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
}

.nav-container .hamburger-close {
    position: absolute;
    padding: 1rem;
    z-index: 25;
    top: 0;
    right: 0;
    display: none;
    cursor: pointer;
}

/*Homepage Content*/

.homepage-container {
    width: 100%;
    max-width: 75rem;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    margin: auto;
}

.homepage-container > div {
    display: inline-block;
}

.homepage-container .text-container {
    margin-left: 3rem;
    flex-basis: 55%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
}

.homepage-container .text-container * {
    flex-basis: 100%;
    margin-bottom: 1rem;
}

.homepage-container .text-container a {
    font-weight: 700;
    color: #ffffff;
    display: inline-block;
    text-align: center;
    width: 30%;
    padding: 1rem 3rem;
    border-radius: 0.4rem;
    transition: 0.1s;
    background: linear-gradient(281.8deg, #00a473 0%, #24e97e 100%);
    background: var(--gradient-green);
    box-shadow: 0px 0px 1rem rgba(30, 222, 125, 0.3);
    box-shadow: var(--shadow-green);
}

.homepage-container .image-container {
    flex-basis: 35%;
    height: 30rem;
}

.homepage-container .image-container img {
    height: 100%;
}

.homepage-container .text1 {
    color: #0076da;
    font-weight: 800;
    font-size: 1.8rem;
}

.homepage-container .text2 {
    color: #002341;
    font-weight: 800;
}

.homepage-container .text2.mobile {
    display: none;
}

.homepage-container .text2.desktop {
    display: block;
}

.homepage-container .text3 {
    color: #98a8b5;
    font-size: 1.2rem;
    line-height: 1.5rem;
}

