        margin: auto;
        width: 30rem;
        padding: 50%;
        height: 0;
        max-height: 0;
        max-width: 100%;
        border-radius: 2rem;
    }
    .dashboardmain-container .button {
        width: 100%;
    }
    .dashboardinput-container * {
        margin-left: 0;
    }
    .dashboardinput-container > div {
        flex-basis: 100% !important;
    }
    /*Patient List*/
    .filter,
    .filter-section {
        flex-flow: column nowrap;
    }
    .filter div,
    .filter-section div {
        flex-basis: 100%;
        width: 100%;
        display: flex;
        margin-bottom: 1rem;
    }
    .search label,
    .filterring label,
    .filter-section .filter-by-status label {
        flex-basis: 20%;
    }
    .search input,
    .filterring select,
    .filter-section .filter-by-status select {
        flex: 1 1;
    }
    .paginate * {
        flex: 1 1;
    }
    .paginate button {
        flex-basis: 40%;
    }
    /*Modal Popup*/
    .modal-content {
        width: 90%;
        padding: 1.5rem;
    }
    .modal-content .symptoms > p {
        flex-basis: 40%;
        font-size: 1.2rem;
        line-height: 115%;
        margin-bottom: 1rem;
    }
    /*Prescriptioning*/
    .fieldbig label,
    .fieldbig input {
        display: block;
    }
    .fieldbig select,
    .medicine-controller select {
        width: 100%;
    }
    .medicine-controller {
        flex-flow: column;
    }
    .medicine-controller > * {
        flex-basis: 100% !important;
    }
    .dashboardcheckbox-container input {
        padding: 0.5rem;
    }
    .patient-addhealth .dashboardtextarea {
        margin-top: 1rem;
    }
    .doctor-container {
        height: 12rem;
        flex-basis: 90%;
    }
    .doctor-container .info-container {
        flex-basis: 45%;
    }
    .doctor-container .picture-container {
        flex-basis: 40%;
    }
    .doctor-container > .picture-container > div {
        height: 10rem;
        width: 10rem;
        background-size: 110% 110%;
        border: 5px solid #ffffff;
    }
    .doctor-container .info-container h3 {
        font-size: 1.2rem;
        margin-bottom: 0.3rem;
    }
    .doctor-container .info-container h1 {
        font-size: 1.7rem;
    }
    #savemedicineform {
        margin-left: 0;
    }
    .home-container {
        display: inline-block;
    }
}
@media screen and (max-width: 1156px) {
    .healthdeclare-container {
        width: 100%;
        overflow-x: scroll;
    }
    .patients-container {
        width: 100%;
        overflow-x: scroll;
    }
}

